.container {
  z-index: 1200;
  background-color: rgba(0, 0, 0, 0.737);
  margin: 0;
  left: 0;
  top: 0;
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .loader {
    position: absolute;
    font-size: 2rem;
  }
}
